<template>
  <v-card>
    <v-card-title>
      <span class="headline" v-text="isEdit ? '' : ''"></span>
    </v-card-title>
    <v-divider/>
    <v-card-text class="px-3">
      <v-form v-model="valid" ref="myBrandForm">
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex class="md6">
              <v-text-field v-model="brand.name" label="" required :rules="nameRules"/>
            </v-flex>
            <v-flex class="md6">
              <v-text-field v-model="brand.letter" label="" required :rules="letterRules"/>
            </v-flex>
            <v-flex>
              <v-cascader
                url="/item/category/of/parent"
                multiple
                required
                v-model="brand.categories"
                label=""/>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex md4>
              <span class="subheading font-weight-thin">LOGO</span>
            </v-flex>
            <v-flex>
              <v-upload v-model="brand.image" :multiple="false" :pic-width="250" :pic-height="90"
                        url="/upload/image"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer/>
      <v-btn depressed  small @click="submit"></v-btn>
      <v-btn depressed small @click="clear"></v-btn>
      <v-btn depressed  small @click="close"></v-btn>
    </v-card-actions>
  </v-card>

</template>

<script>
  export default {
    name: "brand-form",
    props: {
      oldBrand: {
        type: Object
      },
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        valid: false, // 
        brand: {
          name: '', // 
          letter: '', // 
          image: '',// logo
          categories: [], // 
        },
        nameRules: [
          v => !!v || "",
          v => /^.{2,}$/.test(v) || "2"
        ],
        letterRules: [
          v => !!v || "",
          v => /^[a-zA-Z]$/.test(v) || "1"
        ]
      }
    },
    methods: {
      submit() {
        // 
        if (this.$refs.myBrandForm.validate()) {
          // brand
          const {categories, letter, ...params} = this.brand;
          // idcategories,id
          params.cids = categories.map(c => c.id).join(",");
          // 
          params.letter = letter.toUpperCase();
          // 
          // this.$http.post('/item/brand', this.$qs.stringify(params))
          this.$http({
            method: this.isEdit ? 'put' : 'post',
            url: '/item/brand',
            data: this.$qs.stringify(params)
          }).then(() => {
            // 
            this.$emit("close");
            this.$message.success("");
          })
            .catch(() => {
              this.$message.error("");
            });
        }
      },
      clear() {
        // 
        this.$refs.myBrandForm.reset();
        // 
        this.categories = [];
      },
      close(){
        this.$emit("close");
      }
    },
    watch: {
      oldBrand: {// oldBrand
        handler(val) {
          if (val) {
            // copy
            this.brand = Object.deepCopy(val)
          } else {
            // brand
            this.brand = {
              name: '',
              letter: '',
              image: '',
              categories: [],
            };
            this.$refs.myBrandForm.reset();
          }
        },
        deep: true
      }
    }
  }
</script>

